import bus from "../../bus/index";

export default {
    state:{
    headersInfo:[
        {
            typeId:0,
            typeIcon:"ios-phone-portrait",
            typeName:"手机"
        },
        {
            typeId:1,
            typeIcon:"ios-tablet-landscape",
            typeName:"平板"
        },
        {
            typeId:2,
            typeIcon:"md-laptop",
            typeName:"笔记本"
        },
        {
            typeId:3,
            typeIcon:"ios-camera-outline",
            typeName:"摄影摄像"
        },
        {
            typeId:4,
            typeIcon:"md-watch",
            typeName:"智能数码"
        }
    ]
        ,
        goodsTypeInfo:[]

    }
    ,
    mutations:{
        SET_TYPE_INFO(state,obj){
        state.goodsTypeInfo = obj
}

    }
    ,
    actions:{
        getTypeInfo({commit}){
            bus.$axios.post("/pawn/type/header").then(
                data=>{
                   // console.log(data.data.data);
                       commit("SET_TYPE_INFO",data.data.data);

                }
            )
        },



    }
    ,
    getters:{

    }
}