import bus from "../../bus";

export default {
    state:{
        typebrand:[],
        typebrandmore:[]
    },
    mutations:{
        SET_TYPE_BRAND(state, obj) {

            state.typebrand = obj



        },
        SET_TYPE_BRAND_MORE(state,obj){
            state.typebrand = state.typebrand.concat(obj)
        }
    },
    actions:{
        getTypeBrand({commit},obj){
            bus.$axios.post("/pawn/brand/typeBrand",obj
            ).then(
                data=>{
                    console.log(data.data);

                    commit("SET_TYPE_BRAND",data.data.rows)

                }
            )
        },
        getTypeBrandMore({commit},obj){
            bus.$axios.post("/pawn/brand/typeBrand",obj
            ).then(
                data=>{
                    console.log(data.data);

                    commit("SET_TYPE_BRAND_MORE",data.data.rows)



                }
            )
        }

    }
}