import bus from "../../bus";

export default {
    state:{
        brandgoodsarray:[]
    },
    mutations:{
        SET_BRAND_GOODS(state, obj) {
            state.brandgoodsarray = obj
        }
    },
    actions:{
        getBrandGoods({commit},obj){
            bus.$axios.post("/pawn/products/productsList?typeId="+ obj.typeId +'&brandId='+obj.brandId +'&offset=' +obj.offset+'&limit=' +obj.limit
            ).then(
                data=>{
                    console.log(data.data.rows);

                    commit("SET_BRAND_GOODS",data.data.rows)

                }
            )
        }

    }
}